export default {
	data() {
		return {
			item:['全部','已成功','失败','处理中'],
			current:0,
			src:'',
			token:'',//onShow时获取token存起来，以便每次发送请求都要重新获取
			orderList:[],
		}
	},
	onShow(){
		this.token = uni.getStorageSync('token')
		console.log(this.token)
		if(!this.token){
			uni.navigateTo({
				url: '../login/login'
			})
			return 
		}
		this.src = this.url
		uni.request({
			url: this.url + '/mobile/getOrderCardFlowList',
			method:'POST',
			header: {
				'content-type': 'application/x-www-form-urlencoded' ,// 默认值
				'token':this.token
			},
			data:{
				type:2,
			},
			success: (res) => {
				console.log(res);
				this.orderList = res.data.data
			}
		})
	},
	methods: {
		change(index){
			this.current = index
			let cardState = ''
			console.log(index)
			if(index != 0){
				cardState = index - 1
			}
			uni.request({
				url: this.url + '/mobile/getOrderCardFlowList',
				method:'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded' ,// 默认值
					'token':this.token
				},
				data:{
					type:2,
					cardState:cardState
				},
				success: (res) => {
					console.log(res);
					this.orderList = res.data.data
				}
			})
		}
	}
}
